.address {
	font-style: normal;
	// text-align: center;
	// @media screen and (min-width: $desktop) {
	//   text-align: start;
	// }
}

.address-icon {
	width: 24px;
	height: 24px;
	margin-right: 16px;
	fill: transparent;
}

.address-item:not(:last-child) {
	margin-bottom: 20px;
}

.address-item:last-child {
	margin-bottom: 40px;
}

.address-link {
	display: flex;
	// align-items: center;
	color: $secondary-grey-color;
	stroke: $secondary-grey-color;
}

.address-link--line {
	text-decoration: underline;
}

.address-link:hover,
.address-link:focus {
	color: $accent-color;
	stroke: $accent-color;
}

.working-hours-text {
	margin: 0;
}
